body {
  background-image: URL("https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSTl2v4ComcnQEv9Aqad-xPrCl-mdIsuyffMg&usqp=CAU");
  background-size: cover;
  background-repeat: no-repeat;
  width: 100vw;
  opacity: 95%;
  box-sizing: border-box;
  overflow-x: hidden;
  color: #343434;
  font-family: "Roboto", "Open Sans", Helvetica, sans-serif;
}

.toggleMenu {
  position: absolute;
  top: 1%;
  right: 2%;
  display: none;
  flex-direction: column;
  justify-content: space-around;
  width: 30px;
  height: 20px;
  border: 1px solid none;
  padding: 5px;
  border-radius: 5px;
}

.toggleMenu:hover {
  cursor: pointer;
}

.bar {
  height: 3px;
  width: 100%;
  background-color: black;
  border-radius: 10px;
}

.App {
  font-family: 'Lato';
  font-size: 18px;
}

.navbar {
  position: absolute;
  top: 0;
  left: 0;
  background-color: #EBD9FF;
  width: 100vw;
  height: 30px;
  padding-left: 20px;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  z-index: 1;
}

.pages {
  padding: 6px 20px;
  font-size: 16px;
  background-color: #E9D7FF;
  border: none;
}

.pages:hover {
  font-weight: bold;
  cursor: pointer;
}

.main {
  display: block;
  position: relative;
  margin: 30px 0;
}

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
  body {
    overflow-x: hidden;
    width: 110vw;
    background-repeat: repeat;
  }

  .toggleMenu {
    display: flex;
    z-index: 1;
  }

  .navbar {
    position: relative;
    top: 20px;
    display: none;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100vw;
    justify-content: center;
    text-decoration: none;
    height: 250px;
  }

  .navbar.active {
    display: flex;
  }

  .pages {
    display: flex;
    flex-direction: column;
    padding: 20px 145px;
    width: 100vw;
  }
}

